@import '../../styles/global.scss';

.icons {
    display: flex;
    align-items: center;

    li {
        display: flex;
        align-items: center;
        width: 35px;
        height: 35px;

        svg {
            width: 35px;
            height: 35px;
        }

        &:not(:first-child) {
            margin-left: 15px;
        }
    }
}

.linksModal {
    width: 270px;
    border-radius: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 18px;
    background-color: $white;
    z-index: 14000;

    .closeBtn {
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 14px;
        right: 10px;
    }

    h4 {
        margin: 0;
        font-size: 18px;
        line-height: 24px;
        color: $black;
        margin-bottom: 15px;
    }
}

.copySuccess {
    background-color: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px 20px 0px 0px;
    height: 40px;
    position: absolute;
    width: 100%;
    bottom: 0;
    color: $black;
    font-size: 14px;
    line-height: 14px;
    animation-name: open;
    animation-duration: 2.5s;
    animation-timing-function: ease-in;
    animation-fill-mode: forwards;
}

@media (min-width: 651px) {
    .copySuccess {
        border-radius: 20px;
        bottom: -52px;
        left: 50%;
        transform: translateX(-50%);
    }
}

@media (min-width: 651px) and (max-width: 1099px) {
    .linksModal {
        width: 320px;
        padding: 16px 24px 24px;
    }

    .icons {
        li {
            width: 46px;
            height: 46px;

            svg {
                width: 46px;
                height: 46px;
            }
        }
    }

    .copySuccess {
        width: 320px;
    }
}

@media (min-width: 1100px) {
    .linksModal {
        width: 380px;
        padding: 20px 30px 25px;
    }

    .icons {
        li {
            width: 52px;
            height: 52px;

            svg {
                width: 52px;
                height: 52px;
            }
        }
    }

    .copySuccess {
        width: 380px;
    }
}

.modalBackground {
    position: fixed;
    z-index: 13000;
    right: 0px;
    bottom: 0px;
    top: 0px;
    left: 0px;
    background-color: rgba($black, .3)
}

@keyframes open {
    0% {
        display: none;
        opacity: 0;
    }

    1% {
        display: block;
        opacity: 0;
    }

    20% {
        display: block;
        opacity: 1;
    }

    80% {
        display: block;
        opacity: 1;
    }

    99% {
        display: block;
        opacity: 0;
    }

    100% {
        display: none;
        opacity: 0;
    }
}
